/**
 * Ortam deikenlerine gvenli eriim salayan yardmc fonksiyonlar
 * Browser ve server ortamlarnda tutarl davran iin tasarlanmtr
 */
export const isServer = typeof window === 'undefined'
export const isDevelopment = process.env.NODE_ENV !== 'production'

/**
 * Ortam deikenine gvenli eriim salar
 * @param key - Ortam deikeni ad
 * @param defaultValue - Deiken tanml deilse kullanlacak varsaylan deer
 */
export const getEnvironmentVariable = (key: string, defaultValue: string = ''): string => {
  if (typeof process !== 'undefined' && process.env && process.env[key]) {
    return process.env[key] as string
  }
  return defaultValue
}
